body {
  background-image: url("http://www.designbolts.com/wp-content/uploads/2013/02/Noise-Light-Grey-Tileable-Pattern-For-Website-Background.jpg"); }

#root {
  width: 900px;
  height: 700px;
  margin: auto; }

h1 {
  text-align: center;
  font-family: 'Bungee', cursive;
  margin: -5px auto;
  border-bottom: 2px solid #FF8B00;
  width: 450px; }

h3 {
  text-align: center;
  font-family: 'Bungee', cursive; }

.counter {
  color: #FF8B00; }

.grid {
  line-height: 1px;
  margin-top: 10px;
  -webkit-box-shadow: 0px 3px 3px 2px #666;
          box-shadow: 0px 3px 3px 2px #666; }

.cell {
  border: 1px solid #666;
  width: 16px;
  height: 16px;
  display: inline-block;
  cursor: pointer;
  background-color: #fff; }

.alive {
  background-color: #FF8B00; }

.dead {
  background-color: #fff; }

.buttons {
  display: block;
  margin-bottom: -10px; }

.playButtons {
  position: relative;
  float: left; }

.otherButtons {
  position: relative;
  float: right; }

.myButton {
  font: 10px 'Bungee', cursive;
  padding: 6px 20px;
  margin: 1px;
  cursor: pointer;
  border: 2px solid #FF8B00;
  border-radius: 20px;
  background-color: #ddd; }

.myButton:hover {
  background-color: #eee;
  color: #FF8B00; }



